import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ListItem from "@mui/material/ListItem";

const dispatchedEvents = [];

const SrsMuiListItem = function (props, ref) {
  return (
    <ListItem
      disablePadding={props.disablePadding}
      dense={props.dense}
      button={props.button}
      selected={props.selected}
      onClick={props.onClick}
      theme={props.muiTheme}
    >
      <slot />
    </ListItem>
  );
};

const styles = `
  /* Custom styles for the MuiListItem component */
`;

const props = [
  {
    name: "disablePadding",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "dense",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "button",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "selected",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onClick",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to ListItem if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiListItem),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
